.nav {
  --box-shadow: 3px 3px 3px rgba(0, 0, 0,.3);
  display: flex;
  .nav-container {
    margin: 1% 6%;
    flex: 1;
    background-color: white;
    display: flex;
    border-radius: 7px;
    z-index: 99;
    // logo
    .nav-container-logo {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      margin: 0.8vw 1.6vw;
      cursor: pointer;
      transition: all .3s;
      &:hover {
        text-shadow: var(--box-shadow);
      }
      .nav-container-logo-main {
        .logo {
          font-size: 2.4vw;
          line-height: 2.4vw;
          white-space: nowrap;
        }  
      }
      .nav-container-logo-base {
        font-size: 10px;
        line-height: 10px;
        margin-bottom: 4px;
        white-space: nowrap;
      }
    }
    .mask {
      flex: 1;
    }
    // 跳转路由
    .nav-container-items {
      display: flex;
      align-items: center;
      font-size: 1.5vw;
      margin-right: 50px;
      position: relative;
      .item-active {
        position: absolute;
        transition: all .3s;
        height: calc(1em + 0.8vw);
        background-color: var(--main-color);
        box-shadow: inset var(--box-shadow);
        background-color: var(--main-color);
        border-radius: 0.5vw;
      }
      .nav-container-items-item {
        margin: 0 3vw;
        padding: 0.4vw 0.5vw;
        transition: all .3s;
        z-index: 9;
        &:hover {
          text-shadow: var(--box-shadow);
        }
        &.active {
          color: white;
          text-shadow: var(--box-shadow);
          &:hover {
            color: black;
          }
        }
      }
    }
  }
}
@primary-color: #FF2A42;