// 设置字体
@font-face {
  font-family: "Mononoki";
  src: url('../assets/font/mononoki-Regular.ttf'),
       url('../assets//font//mononoki-Bold.ttf'),
       url('../assets//font//mononoki-Italic.ttf');
}

@font-face {
  font-family: "微软雅黑";
  src: url('../assets/font/msyh.ttc'),
       url('../assets/font//msyhbd.ttc'),
       url('../assets//font/msyhl.ttc');
}

@font-face {
  font-family: "方正经黑简体";
  src: url('../assets/font/方正经黑简体.otf');
}

* {
  font-family: "Mononoki","微软雅黑";
}

:root {
  --main-color: #FF9A3E;
  --deep-blue: #1B325D;
  --box-shadow: 3px 3px 6px rgba(0, 0, 0,.25);
}

html {
  background-color: var(--main-color);
}

.App {
  position: relative;
  // min-height: 100vh;
  // min-width: 768px;
}

.router {
  padding-bottom: 15%;
}

// 阴影
.shadow {
  box-shadow: 0px 5px 5px 1px rgba(0, 0, 0, 0.1);
}

.inset-shadow {
  box-shadow: inset 0px 5px 5px 1px rgba(0, 0, 0, 0.06);
}

// 字体颜色
.font-main-color {
  color: var(--main-color);
}

.font-blue-color {
  color: var(--deep-blue);
}

// 字体样式
.logo {
  font-family: "方正经黑简体" !important;
}

.base {
  font-family: "微软雅黑" !important;
}

.bold {
  font-weight: bold;
}
@primary-color: #FF2A42;