.footer {
  --box-shadow: 3px 3px 3px rgba(0, 0, 0,.3);
  background-color: #039FEA;
  border-radius: 50%;
  position: absolute;
  top: 50%; 
  left: 0;
  width: 100%;
  height:100%;
  clip-path: inset(0 0 50% 0); 
  position:absolute;
  .footer-white {
    width: 100%;
    padding-bottom: 45%;
    background-color: white;
    border-radius: 50%;
    transform: translateY(20%);
  }
  .footer-white-face {
    width: 26%;
    padding-bottom: 50%;
    transform: translateY(-100%);
    margin: 0 auto;
    display: flex;
    .footer-white-face-eyes {
      width: 100%;
      padding-bottom: 50%;
      position: absolute;
      top: 16%;
      display: flex;;
      .footer-white-face-eye {
        position: absolute;
        width: 50%;
        height: 100%;
        background-color: #F0F0F0;
        border-radius: 50%;
        box-shadow: var(--box-shadow);
        &.eye-left {
          transform: translateX(-2.5%);
        }
        &.eye-right {
          left: 50%;
          transform: translateX(2.5%);
        }
        .footer-white-face-eye-soul {
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50%,-50%);
          width: 40%;
          height: 40%;
          border-radius: 50%;
          background-color: black;
          box-shadow: var(--box-shadow);
          .footer-white-face-eye-soul-light {
            position: absolute;
            top: 0;
            right: 0;
            width: 62.5%;
            height: 62.5%;
            border-radius: 50%;
            background-color: white;
            box-shadow: var(--box-shadow);
          }
        }
        .footer-white-face-eye-tear {
          width: 45%;
          height: 15%;
          border-radius: 50%;
          background-color: white;
          box-shadow: var(--box-shadow);
          position: absolute;
          bottom: -5%;
          left: 50%;
          &.tear-left {
            transform: translateX(-60%);
          }
          &.tear-right {
            transform: translateX(-40%);
          }
        }
      }
      .footer-white-face-nose {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: -28%;
        border-radius: 50%;
        width: 30%;
        padding-bottom: 30%;
        z-index: 1;
        background-color: #E75605;
        box-shadow: var(--box-shadow);
        .footer-white-face-nose-light {
          width: 40%;
          height: 40%;
          background-color: white;
          position: absolute;
          top: 10%;
          right: 10%;
          border-radius: 50%;
        }
      }
    }
  }
}

.font-container {
  position: absolute;
  bottom: 0;
  width: 104%;
  padding-bottom: 45%;
  margin: 0 -2%;
  overflow: hidden;
}
@primary-color: #FF2A42;